import { useState, useEffect } from 'react';
import Navbar from './Navbar';
import './Navbar.css';
import { useNavigate, Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCircleDot,
  faEdit,
  faTrash,
} from '@fortawesome/free-solid-svg-icons';

const Uploaded_Opportunities = () => {
  const [opportunities, setOpportunities] = useState([]);

  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');

    fetch('http://localhost:4000/api/opportunity/all/patron', {
      method: 'GET',
      headers: { Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setOpportunities([...res.data]);
      });
  }, [opportunities]);

  const handleEditOpportunity = (opportunityId) => {
    navigate(`/Edit_Opportunity/${opportunityId}`);
  };

  // console.log(opportunities);

  return (
    <div>
      <nav className="navbar bg-light sticky-top">
        <div className="container-fluid">
          <div className="left-components">
            <Navbar />
            <Link
              className="navbar-brand"
              style={{ marginLeft: '57px', position: 'fixed' }}
              to="index.html"
            >
              <span className="text-danger text">
                <strong>ekala</strong>
              </span>
              kaar
            </Link>
          </div>
          <div
            className="right-component"
            style={{ right: '10px', top: '10px', position: 'fixed' }}
          >
            <form className="d-flex" role="search">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>

      <div className="container mt-5 mb-5 d-flex justify-content-center flex-column gap-5">
        {<h5 class="card-title "> <h1>Total: {opportunities.length} Opportunities Posted </h1></h5>}
        {opportunities.map((opp, index) => {
          return (
            <div class="card" key={index}>
              <div class="card-header h2 bg-danger text-capitalize">Position - {opp.position}</div>
              <div class="card-body">
                {/* <h5 class="card-title">{opp.about}</h5> */}
                {/* <p class="card-text">{opp.requirements}</p> */}
                <p class="card-text  text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Location:</strong> {opp.location}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Mode:</strong> {opp.mode}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Openings:</strong> {opp.number}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Language:</strong> {opp.language}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Budget:</strong> {opp.budget}
                </p>
                <div className="d-flex float-right">
                  <button
                    className="btn btn-outline-danger btn-sm"
                    onClick={() => handleEditOpportunity(opp._id)}
                  >
                    <FontAwesomeIcon icon={faEdit} className="mr-1" /> Edit
                  </button>
                  <button className="btn btn-outline-danger btn-sm ml-2">
                    <FontAwesomeIcon icon={faTrash} className="mr-1" /> Delete
                  </button>
                  <button className="btn btn-outline-danger btn-sm ml-2">
                    <FontAwesomeIcon icon={faTrash} className="mr-1" /> Disable
                  </button>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Uploaded_Opportunities;
