import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import React from "react";
import { Link } from "react-router-dom";
import "./Frontpage.css";
import "./Dashboard.css";
function Navbar_frontpage() {
  const mystyle = {
    fontSize: "large",
    fontWeight: "500",
  };
  const back = {
    backgroundColor: "white",
  };
  return (
    <Navbar
      style={{ zIndex: "99" }}
      className="navbar nav_frontpage navbar-expand-lg "
      // bg="light"
      expand="lg"
    >
      {/* <Container> */}
      <div className="container-fluid" >
        <Link to="/">
          <Navbar.Brand
            className="navbar-brand"
            style={{ position: "static", marginLeft: "0px" }}
          >
            {" "}
            <img
              src="assets/images/ekalakaa.png"
              height="50"
              width="70"
              alt="logo"
            />
           
          </Navbar.Brand>
        </Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav" style={mystyle}>
          <div className="navbar-nav" style={back}>
            <Nav className="me-auto">
              {/* <Link
                to="/"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
                About us
              </Link> */}
              <Link
                to="/Patrondesc"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
                Patrons
              </Link>
              <Link
                to="/Artistdesc"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
                Artists
              </Link>
              <Link
                to="/Partnerdesc"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
                Partners
              </Link>
              <Link
                to="/Artloversdesc"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
                Art Lovers
              </Link>
              <Link to="/photo" className="nav-item nav-link pl-3" style={{color:"#e21818"}}>Media</Link>
              <Link
                to="/Join"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
               Join us
              </Link>
              <Link
                to="/Contact"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
                Contact us
              </Link>
              

              <Link
                to="/Media"
                className="nav-item nav-link pl-3"
                style={{ color: "#e21818" }}
              >
               Latest Events
              </Link>
            </Nav>
          </div>
          <div className="navbar-nav ms-auto" style={back}>
            <Link
              to="/Login"
              className="nav-item nav-link pl-3 "
              style={{ color: "#e21818" }}
            >
              Login/Join
            </Link>
          </div>
        </Navbar.Collapse>
        {/* </Container> */}
      </div>
    </Navbar>
  );
}

export default Navbar_frontpage;
