import React, { useEffect, useState } from 'react';
import Navbar from './Navbar';
import { Navigate, useParams } from 'react-router-dom';
import './Navbar.css';
import './Patron.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Edit_Opportunity() {
  const [oppData, setOppData] = useState({});
  const [updateUserData, seUpdateUserData] = useState({});

  const id = useParams();

  useEffect(() => {
    const token = localStorage.getItem('token');

    fetch(`http://localhost:4000/api/opportunity/${id.id}`, {
      method: 'GET',
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setOppData(res.data);
      });
  }, [id]);

  const userDataChangeHandler = (event) => {
    seUpdateUserData((prevState) => {
      return { ...prevState, [event.target.name]: event.target.value };
    });
  };

  console.log(id);

  const notify = () => toast('Edited succesfully!');

  const handleSubmit = async (e) => {
    const token = localStorage.getItem('token');

    e.preventDefault();
    fetch(`http://localhost:4000/api/opportunity/${id.id}`, {
      method: 'PATCH',
      body: JSON.stringify(updateUserData),
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setOppData((prevState) => {
          return { ...prevState, ...res.data };
        });
      });

    notify();

    setTimeout(() => {
      Navigate('/Uploaded_opportunities');
    }, 2000);
  };

  // console.log(updateUserData);

  const type = ['Solo', 'Group'];
  const languages = ['English', 'Hindi', 'Marathi', 'Other'];
  const modes = ['Online', 'Offline', 'Hybrid'];

  const dateConvert = (dateString) => {
    const date = new Date(dateString);
    const year = date.getFullYear();
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const day = date.getDate().toString().padStart(2, '0');
    const formattedDate = `${year}-${month}-${day}`;
    // console.log(formattedDate);
    return formattedDate;
  };

  // console.log(oppData);

  // console.log(`http://localhost:4000/api/opportunity/${id.id}`);

  return (
    <div>
      <nav
        className="navbar bg-light fixed-top"
        style={{ backgroundColor: 'white' }}
      >
        <div className="container-fluid">
          <div className="left-components">
            <Navbar className="nav_artist" />
            <a className="navbar-brand " href="/">
              <span className="text-danger text">
                {' '}
                <strong>ekala</strong>
              </span>
              kaar
            </a>
          </div>
          <div className="right-component">
            <form className="d-flex" role="search">
              <input
                className="form-control me-2 "
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>

      <div className="patron-upload-opportunity container">
        {!oppData && <h5>Opportunity not found!</h5>}
        {oppData && (
          <>
            <div className="patron-upload-opportunity-heading text-center">
              Edit opportunity
            </div>
            <div style={{ borderWidth: '1px' }} className="card">
              <div className="card-body">
                <form
                  action="uploaded-opportunities.html"
                  onSubmit={handleSubmit}
                >
                  <div className="mb-3 row">
                    <label
                      for="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Position
                    </label>
                    <div className="col-sm-10">
                      <input
                        type="text"
                        className="form-control patron-upload-opportunity-inputPosition"
                        defaultValue={oppData.position}
                        onChange={userDataChangeHandler}
                        name="position"
                        id="position"
                      />
                    </div>
                  </div>
                  <div className="mb-3">
                    <label
                      for="aboutCompany"
                      className="form-label patron-upload-opportunity-form-label"
                    >
                      About Company
                    </label>
                    <textarea
                      className="form-control"
                      id="aboutCompany"
                      defaultValue={oppData.about}
                      onChange={userDataChangeHandler}
                      name="about"
                      rows="3"
                    ></textarea>
                  </div>
                  <div className="mb-3">
                    <label
                      for="Requirements"
                      className="form-label patron-upload-opportunity-form-label"
                    >
                      Requirements
                    </label>
                    <textarea
                      className="form-control"
                      id="requirements"
                      defaultValue={oppData.requirements}
                      onChange={userDataChangeHandler}
                      name="requirements"
                      rows="3"
                    ></textarea>
                  </div>
                  <div className="mb-3 row">
                    <label
                      for="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Duration
                    </label>
                    <div>
                      <div className="col-sm-10">
                        <label
                          for="StartDate"
                          className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                        >
                          Start Date
                          <input
                            type="date"
                            defaultValue={dateConvert(oppData?.duration?.start)}
                            onChange={userDataChangeHandler}
                            name="start"
                            id="startdate"
                            style={{ border: '1px solid 	#B2BEB5' }}
                          />
                        </label>
                      </div>
                      <label
                        for="EndDate"
                        className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                      >
                        End Date
                        <input
                          type="date"
                          defaultValue={dateConvert(oppData?.duration?.end)}
                          onChange={userDataChangeHandler}
                          name="end"
                          id="enddate"
                          style={{ border: '1px solid 	#B2BEB5' }}
                        />
                      </label>
                    </div>
                  </div>
                  <div className="mb-3 row">
                    <label
                      for="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Number
                    </label>
                    <div className="col-sm-10">
                      <input
                        type="text"
                        className="form-control patron-upload-opportunity-inputNumber"
                        defaultValue={oppData.number}
                        onChange={userDataChangeHandler}
                        name="budget"
                        id="number"
                      />
                    </div>
                  </div>
                  <div className="mb-3 row">
                    <label
                      htmlFor="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Budget
                    </label>
                    <div className="col-sm-5">
                      <input
                        type="number"
                        className="form-control patron-upload-opportunity-inputNumber"
                        id="budget"
                        defaultValue={oppData.budget}
                        onChange={userDataChangeHandler}
                        name="budget"
                        required
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      htmlFor="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Choose Type
                    </label>
                    <div className="col-sm-5">
                      <select
                        className="form-select"
                        defaultValue={oppData?.type}
                        onChange={userDataChangeHandler}
                        name="type"
                        required
                      >
                        <option selected value="choose">
                          Choose...
                        </option>
                        {oppData.type &&
                          type.map((type, index) => {
                            if (type === oppData.type) {
                              return (
                                <option
                                  selected
                                  defaultValue={type}
                                  key={index}
                                >
                                  {oppData.type}
                                </option>
                              );
                            } else {
                              return (
                                <option defaultValue={type} key={index}>
                                  {type}
                                </option>
                              );
                            }
                          })}
                      </select>
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      for="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Language
                    </label>
                    <div className="col-sm-5">
                      <input
                        type="language"
                        className="form-control patron-upload-opportunity-inputNumber"
                        id="language"
                        defaultValue={oppData.language}
                        onChange={userDataChangeHandler}
                        name="language"
                        required
                      />
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      for="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Mode
                    </label>
                    <div className="col-sm-5">
                      <select
                        className="form-select"
                        defaultValue={oppData.mode}
                        onChange={userDataChangeHandler}
                        name="mode"
                        required
                      >
                        <option selected value="choose">
                          Choose Mode
                        </option>
                        {oppData.mode &&
                          modes.map((mode, index) => {
                            if (mode === oppData.mode) {
                              return (
                                <option
                                  key={index}
                                  selected
                                  defaultValue={mode}
                                >
                                  {oppData.mode}
                                </option>
                              );
                            } else {
                              return (
                                <option key={index} defaultValue={mode}>
                                  {mode}
                                </option>
                              );
                            }
                          })}
                      </select>
                    </div>
                  </div>

                  <div className="mb-3 row">
                    <label
                      htmlFor="position"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label-small-input-box"
                    >
                      Location
                    </label>
                    <div className="col-sm-10">
                      <input
                        type="text"
                        className="form-control patron-upload-opportunity-inputNumber"
                        id="location"
                        title="Please fill the input properly"
                        defaultValue={oppData.location}
                        onChange={userDataChangeHandler}
                        name="location"
                        required
                      />
                    </div>
                  </div>
                  <div className="mb-3">
                    <label
                      for="link-to-document"
                      className="col-sm-2 col-form-label patron-upload-opportunity-form-label"
                    >
                      Link to document
                    </label>
                    <div className="col-sm-10">
                      <input
                        type="text"
                        className="form-control"
                        defaultValue={oppData?.link_of_document}
                        onChange={userDataChangeHandler}
                        name="link_of_document"
                        id="link-to-document"
                      />
                    </div>
                  </div>
                  <div className="d-grid gap-1 d-md-flex justify-content-center patron-upload-opportunity-upload-button">
                    <button
                      className="btn btn-danger me-md-2 btn-new"
                      type="submit"
                      value="Submit"
                    >
                      Save changes
                    </button>
                  </div>
                  <ToastContainer />
                </form>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
