import React, { Component } from "react";
import ArtLover_Navbar from "./ArtLover_Navbar";
import { useState, useEffect } from "react";
import "./Navbar.css";
import "./Patron.css";
import { Link, useNavigate } from "react-router-dom";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export default function Edit_ArtLover_profile(props) {
  const navigate = useNavigate();
  const { userInfo, setUserInfo } = props;
  const [updateUserInfo, setUpdateUserInfo] = useState({
    fname: userInfo.fname,
    mname: userInfo.mname,
    lname: userInfo.lname,
    phone: userInfo.phone,
    email: userInfo.email,
    age: userInfo.age,
    gender: userInfo.gender,
    artCategory: userInfo.art,
    link: userInfo.link,
    location: userInfo.location
  })

  const onChange = (e) => {
    setUpdateUserInfo({ ...updateUserInfo, [e.target.name]: e.target.value });
  }

  const handleSubmitEdit = (e) => {
    e.preventDefault();
    updateDatabase();
    navigate("/ArtLover_dashboard");
  }

  const updateDatabase = async () => {
    const response = await fetch("http://localhost:4000/api/user", {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        "id": localStorage.getItem("_id"),
        "role": localStorage.getItem("role"),
        "token": localStorage.getItem("token")
      },
      body: JSON.stringify({
        age: updateUserInfo.age,
        location: updateUserInfo.location,
        gender: updateUserInfo.gender,
        artCategory: updateUserInfo.artCategory,
        link: updateUserInfo.link
      })
    })
    const json = await response.json();
    console.log(json);
  }

  return (
    <div>
      <nav
        className="navbar bg-light fixed-top"
        style={{ backgroundColor: "white" }}
      >
        <div className="container-fluid">
          <div className="left-components">
            <ArtLover_Navbar className="nav_artist" />
            <a className="navbar-brand " href="/">
              <span className="text-danger text">
                {" "}
                <strong>ekala</strong>
              </span>
              kaar
            </a>
          </div>
          <div className="right-component">
            <form className="d-flex" role="search">
              <input
                className="form-control me-2 "
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>


      <div className="edit-patron-profile">
        <div className="text-center text-danger edit-patron-profile-text">
          EDIT YOUR PROFILE
        </div>
        <div className="container patron-center">
          <div style={{ borderWidth: "1px" }} className="card">
            <div className="card-body ">
              <form
                style={{ marginLeft: "20px" }}
                onSubmit={handleSubmitEdit}
                method="patch"
              >
                <div className="row justify-content-start patron-profile-photo-container">
                  <div className="col">
                    <img alt=""
                      className="patron-profile-photo"
                      id="chosen-img"
                      src="assets/images/Linkb11.jpeg"
                    />

                    {/* <input type="file" id="upload-button" accept="image/*" style={{display: "none"}}
                                    value="img"/> */}

                    <input
                      type="file"
                      name="file"
                      id="file"
                      className="inputfile" /* onChange={uploadImage} */
                    />
                    <label htmlFor="file">Choose a file</label>
                  </div>
                </div>

                <div className="row">
                  <div
                    className="row container edit-patron-profile-input-rows"
                    id="inputs"
                    style={{ marginBottom: " 10px" }}
                  >
                    <div className="col-12 text-end" style={{ margin: "2%" }}>
                      <Link
                        to=""
                        style={{ color: "black", textDecoration: "none" }}
                      >
                        <i className="bi bi-pen-fill "></i>
                      </Link>
                    </div>

                    <div className="col space">
                      <label
                        htmlFor="validationCustom01"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        First name
                      </label>
                      <input
                        type="text"

                        //onBlur={(e)=> checkName(e.target.value)}
                        className="form-control"
                        id="firstname"
                        required
                        pattern="[A-Za-z]+"
                        title="Only alphabet should be present"
                        value={updateUserInfo.fname}
                        onChange={onChange}
                        name="fname"
                        disabled
                      />
                      {/* {nameError ? <span>Invalid name</span> : <></>} */}
                    </div>
                    <div className="col space">
                      <label
                        htmlFor="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Last name
                      </label>
                      <input
                        type="text"

                        //onBlur={(e)=> checkName(e.target.value)}
                        className="form-control"
                        id="lastname"
                        required
                        pattern="[A-Za-z]+"
                        title="Only alphabets should be present"
                        value={updateUserInfo.lname}
                        onChange={onChange}
                        name="lname"
                        disabled
                      />
                      {/* {nameError ? <span>Invalid name</span> : <></>} */}
                    </div>
                  </div>
                  <div
                    className="row container edit-patron-profile-input-rows"
                    style={{ marginBottom: "10px" }}
                  >
                    <div className="col space">
                      <label
                        htmlFor="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Phone number
                      </label>
                      <input
                        type="tel"

                        className="form-control"
                        pattern="[1-9]{1}[0-9]{9}"
                        id="phone"
                        required
                        title="10 digits should be present"
                        value={updateUserInfo.phone}
                        onChange={onChange}
                        name="phone"
                        disabled
                      />
                      {/* {phoneError ? <span>Invalid Number</span> : <></>} */}
                    </div>
                    <div className="col space">
                      <label
                        htmlFor="validationCustomUsername"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Email
                      </label>
                      <div className="input-group has-validation">
                        <span
                          className="input-group-text"
                          id="inputGroupPrepend"
                        >
                          @
                        </span>
                        <input
                          type="text"

                          className="form-control"
                          id="email"
                          aria-describedby="inputGroupPrepend"
                          disabled
                          value={updateUserInfo.email}
                          onChange={onChange}
                          name="email"
                        />
                      </div>
                    </div>
                  </div>
                  <div
                    className="row container edit-patron-profile-input-rows"
                    style={{ marginBottom: "10px" }}
                  >
                    <div className="col space">
                      <label
                        htmlFor="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Age

                      </label>
                      <input
                        type="number"
                        className="form-control"
                        id="age"
                        value={updateUserInfo.age}
                        onChange={onChange}
                        name="age"
                      />
                    </div>
                    <div className="col space">
                      <label
                        htmlFor="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Location

                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="location"
                        value={updateUserInfo.location}
                        onChange={onChange}
                        name="location"
                      />
                    </div>

                  </div>

                  <div
                    className="row container edit-patron-profile-input-rows"
                    style={{ marginBottom: "10px" }}
                  >
                    <div className="col space">
                      <label
                        htmlFor="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Art

                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="artCategory"
                        value={updateUserInfo.artCategory}
                        onChange={onChange}
                        name="artCategory"
                      />
                    </div>
                    <div className="col space">
                      <label
                        htmlFor="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Link Provided

                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="link"
                        value={updateUserInfo.link}
                        onChange={onChange}
                        name="link"
                      />
                    </div>

                  </div>
                  <div className="col space">
                    <label
                      htmlFor="validationCustom02"
                      className="form-label edit-patron-profile-input-lable"
                    >
                      Gender

                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="gender"
                      value={updateUserInfo.gender}
                      onChange={onChange}
                      name="gender"
                    />
                  </div>
                  <div
                    className="position-relative edit-patron-profile-save-changes"
                    style={{ marginTop: "40px" }}
                  >
                    <ToastContainer />
                    <input
                      className="btn btn-danger btn-new"
                      type="submit"
                    ></input>

                    <div className="position-absolute bottom-0">
                      {/* <Link
                       // to="/Patron_dashboard"
                        className="btn btn-danger btn-new"
                        type="submit"
                        value="Submit"
                      >
                        Save Changes
                      </Link> */}
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>


    </div>
  )
}
