import React, { Component } from "react";
import { useState } from "react";

import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

export default function Admin_Navbar() {

    const [sidebar, setSidebar] = useState(false);
    const showSidebar = () => setSidebar(!sidebar);
  
    const navigate = useNavigate();
    const handleLogout = async () => {
      console.log("Logging out");
  
      localStorage.clear();
  
      navigate("/Login");
    };
  
  return (
    <div>




<div className="navofart">
      <div className="navbar">
        {/* <FaIcons.FaBars onClick={showSidebar}/> */}
        <div className="menu-bars">
          <i class="fa-solid fa-bars" onClick={showSidebar}></i>
        </div>
      </div>
      <nav className={sidebar ? "nav-menu-active" : "nav-menu"}>
        <ul
          className="nav-menu-items list-group list-group-flush"
          onClick={showSidebar}
        >
          <li className="navbar-toggle ">
            <div className="menu-bars">
              <i class="fa-solid fa-xmark"></i>
            </div>
          </li>
         
          <li className="nav-text list-group-item">
            <Link to="/Admin_dashboard" className="menu-bar-options">
              Dashboard
            </Link>
          </li>
          <li className="nav-text list-group-item">
            <Link to="/Admin_View_ArtistList" className="menu-bar-options">
              View Artist
            </Link>
          </li>
          <li className="nav-text list-group-item">
            <Link to="/Admin_view_PatronList" className="menu-bar-options">
              View Patron
            </Link>
          </li>
        
        
          
         
          <li className="nav-text list-group-item">
            <Link
              to="/Login"
              onClick={handleLogout}
              type="submit"
              className="menu-bar-options"
            >
              Logout
            </Link>
          </li>

        </ul>
      </nav>
    </div>
    </div>



  )
}
