import React from 'react';
import Navbar from './Navbar';
import { useState, useEffect } from 'react';
import './Navbar.css';
import './Patron.css';
import { Link, useNavigate } from 'react-router-dom';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default function Edit_patron_profile() {
  const [userData, setUserData] = useState({});
  const [updateUserData, seUpdateUserData] = useState({});

  useEffect(() => {
    const token = localStorage.getItem('token');

    fetch('http://localhost:4000/api/user', {
      method: 'GET',
      headers: { Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setUserData(res);
      });
  }, []);

  // console.log(userData);

  const userDataChangeHandler = (event) => {
    seUpdateUserData((prevState) => {
      return { ...prevState, [event.target.name]: event.target.value };
    });
  };

  // console.log(updateUserData);

  const notify = () => toast('Edited succesfully!');

  let navigate = useNavigate();

  const handleSubmit = async (e) => {
    const token = localStorage.getItem('token');

    e.preventDefault();

    fetch('http://localhost:4000/api/user', {
      method: 'PATCH',
      body: JSON.stringify(updateUserData),
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setUserData((prevState) => {
          return { ...prevState, ...res.data };
        });
      });

    notify();

    setTimeout(() => {
      navigate('/Patron_dashboard');
    }, 1000);
  };

  return (
    <div>
      <nav
        className="navbar bg-light fixed-top"
        style={{ backgroundColor: 'white' }}
      >
        <div className="container-fluid">
          <div className="left-components">
            <Navbar className="nav_artist" />
            <a className="navbar-brand " href="/">
              <span className="text-danger text">
                {' '}
                <strong>ekala</strong>
              </span>
              kaar
            </a>
          </div>
          <div className="right-component">
            <form className="d-flex" role="search">
              <input
                className="form-control me-2 "
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search"
                type="submit"
                onSubmit={handleSubmit}
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>
      <div className="edit-patron-profile">
        <div className="text-center text-danger edit-patron-profile-text">
          Edit Your Profile Data
        </div>
        <div className="container patron-center">
          <div style={{ borderWidth: '1px' }} className="card">
            <div className="card-body ">
              <form
                style={{ marginLeft: '20px' }}
                //action="edit-patron-profile.html"
                onSubmit={handleSubmit}
              >
                <div className="row justify-content-start patron-profile-photo-container">
                  <div className="col">
                    <img
                      className="patron-profile-photo"
                      id="chosen-img"
                      src="assets/images/Linkb11.jpeg"
                      alt=""
                    />
                    <input
                      type="file"
                      name="file"
                      id="file"
                      class="inputfile" /* onChange={uploadImage} */
                    />
                    <label for="file">Choose a file</label>
                  </div>
                </div>

                <div className="row">
                  <div
                    className="row container edit-patron-profile-input-rows"
                    id="inputs"
                    style={{ marginBottom: ' 10px' }}
                  >
                    <div className="col-12 text-end" style={{ margin: '2%' }}>
                      <Link
                        to=""
                        style={{ color: 'black', textDecoration: 'none' }}
                      >
                        <i className="bi bi-pen-fill "></i>
                      </Link>
                    </div>

                    <div className="col space">
                      <label
                        for="validationCustom01"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        First name
                      </label>
                      <input
                        type="text"
                        // value={userData.name}
                        defaultValue={userData.name}
                        onChange={userDataChangeHandler}
                        name="name"
                        className="form-control"
                        id="firstname"
                        pattern="[A-Za-z]+"
                        title="Only alphabet should be present"
                        required
                      />
                    </div>
                    <div className="col space">
                      <label
                        for="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Last name
                      </label>
                      <input
                        type="text"
                        defaultValue={userData.lastName}
                        onChange={userDataChangeHandler}
                        name="lastName"
                        className="form-control"
                        id="lastname"
                        required
                        pattern="[A-Za-z]+"
                        title="Only alphabets should be present"
                      />
                    </div>
                  </div>
                  <div
                    className="row container edit-patron-profile-input-rows"
                    style={{ marginBottom: '10px' }}
                  >
                    <div className="col space">
                      <label
                        for="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Phone number
                      </label>
                      <input
                        type="tel"
                        defaultValue={userData.phoneNumber}
                        onChange={userDataChangeHandler}
                        name="phoneNumber"
                        className="form-control"
                        pattern="[1-9]{1}[0-9]{9}"
                        id="phone"
                        required
                        title="10 digits should be present"
                      />
                    </div>
                    <div className="col space">
                      <label
                        for="validationCustomUsername"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Email
                      </label>
                      <div className="input-group has-validation">
                        <span
                          className="input-group-text"
                          id="inputGroupPrepend"
                        >
                          @
                        </span>
                        <input
                          type="text"
                          value={userData.email}
                          className="form-control"
                          id="email"
                          aria-describedby="inputGroupPrepend"
                          disabled
                        />
                      </div>
                    </div>
                  </div>
                  <div
                    className="row container edit-patron-profile-input-rows"
                    style={{ marginBottom: '10px' }}
                  >
                    <div className="col space">
                      <label
                        for="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Company name
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="Company-name"
                        defaultValue={userData?.patron?.name_of_company}
                        onChange={userDataChangeHandler}
                        name="name_of_company"
                      />
                    </div>
                    <div className="col space">
                      <label
                        for="validationCustom02"
                        className="form-label edit-patron-profile-input-lable"
                      >
                        Location
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="Company-name"
                        defaultValue={userData?.patron?.location}
                        onChange={userDataChangeHandler}
                        name="location"
                      />
                    </div>
                  </div>
                  <div className="col space">
                    <label
                      for="validationCustom02"
                      className="form-label edit-patron-profile-input-lable"
                    >
                      Website
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="Company-name"
                      defaultValue={userData?.patron?.website_link}
                      onChange={userDataChangeHandler}
                      name="website_link"
                    />
                  </div>
                  <div
                    className="position-relative edit-patron-profile-save-changes"
                    style={{ marginTop: '40px' }}
                  >
                    <ToastContainer />
                    <input
                      className="btn btn-danger btn-new"
                      type="submit"
                    ></input>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
