import React from "react";
import Script from "./Script";
import "./Navbar.css";
import { Link } from "react-router-dom";
import "./Patron.css";
import Navbar from "./Navbar";

export default function Rate_previously_hired_artist() {
  return (
    <div>
      <nav
        className="navbar bg-light fixed-top"
        style={{ backgroundColor: "white" }}
      >
        <div className="container-fluid">
          <div className="left-components">
            <Navbar className="nav_artist" />
            <a className="navbar-brand " href="/">
              <span className="text-danger text">
                {" "}
                <strong>ekala</strong>
              </span>
              kaar
            </a>
          </div>
          <div className="right-component">
            <form className="d-flex" role="search">
              <input
                className="form-control me-2 "
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>

      <div className="rate-previously-hired-artist">
        <div className="rate-previously-hired-artist-header1 text-center">
          How was your experience?
        </div>
        <div className="rate-previously-hired-artist-header2 text-center">
          Let us know by rating the artist!
        </div>
        <div className="rate-previously-hired-artist-body container">
          <form action="#" onsubmit="return(validateRatePatron());">
            <div className="rate-patron-body-patron-rating">
              <div className="row">
                <label
                  htmlFor="staticEmail"
                  className="col-sm-2 form-label rate-patron-body-patron-feedback-text"
                >
                  Rating:
                </label>
                <div class="rate rateside">
                  <input type="radio" id="star5" name="rate" value="5" />
                  <label for="star5" title="text">
                    5 stars
                  </label>
                  <input type="radio" id="star4" name="rate" value="4" />
                  <label for="star4" title="text">
                    4 stars
                  </label>
                  <input type="radio" id="star3" name="rate" value="3" />
                  <label for="star3" title="text">
                    3 stars
                  </label>
                  <input type="radio" id="star2" name="rate" value="2" />
                  <label for="star2" title="text">
                    2 stars
                  </label>
                  <input type="radio" id="star1" name="rate" value="1" />
                  <label for="star1" title="text">
                    1 star
                  </label>
                </div>
                <div className="w-100" />
                <div className="col-sm-10 rate-patron-body-patron-rating-number"></div>
              </div>
            </div>
            <div className="rate-patron-body-patron-feedback">
              <div className="mb-3">
                <label
                  htmlFor="feedback"
                  className="form-label rate-patron-body-patron-feedback-text"
                >
                  Feedback:
                </label>
                <textarea
                  className="form-control"
                  id="feedback"
                  rows={5}
                  defaultValue={""}
                />
              </div>
            </div>
            <div className="d-grid gap-1 d-md-flex justify-content-center edit-patron-profile-save-changes">
              <button
                className="btn btn-danger me-md-2 btn-new"
                style={{ marginTop: "10px" }}
                type="submit"
                value="Submit"
              >
                Submit
              </button>
            </div>
          </form>
        </div>
      </div>

      <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/2.1.3/jquery.min.js"></script>
    </div>
  );
}
