import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import ArtLover_Navbar from "./ArtLover_Navbar";

import "./Navbar.css";
import { Link } from "react-router-dom";
import "./Patron.css";

export default function ArtLover_dashboard(props) {
  const { userInfo, setUserInfo } = props;
  const navigate = useNavigate();
  const handleLogout = async () => {
    console.log("Logging out");

    localStorage.clear();
    setUserInfo({});
    navigate("/Login");
  };

  useEffect(() => {
    loadArtLover();
  }, [])


  const loadArtLover = async () => {
    if (!localStorage.getItem("token")) {
      alert("Login before proceeding");
      navigate("/login");
    }
    const response = await fetch("http://localhost:4000/api/user", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "token": localStorage.getItem("token")
      }
    });
    const data = await response.json()
    if (!data.artLover) {
      setUserInfo({
        fname: data.name,
        mname: data.middleName,
        lname: data.lastName,
        phone: data.phoneNumber,
        email: data.email,
        age: "",
        gender: "",
        art: "",
        link: "",
        location: ""
      })
    }
    else {
      setUserInfo({
        fname: data.name,
        mname: data.middleName,
        lname: data.lastName,
        phone: data.phoneNumber,
        email: data.email,
        age: data.artLover.age,
        gender: data.artLover.gender,
        art: data.artLover.artCategory,
        link: data.artLover.link,
        location: data.artLover.location
      })
    }
  }

  let [size, setSize] = useState(window.screen.width);
  window.onresize = () => {
    setSize(window.innerWidth);
  };

  return (
    <div>
      <nav
        className="navbar bg-light fixed-top"
        style={{ backgroundColor: "white" }}
      >
        <div className="container-fluid">
          <div className="left-components">
            <ArtLover_Navbar className="nav_artist" />
            <a className="navbar-brand " href="/">
              <span className="text-danger text">
                {" "}
                <strong>ekala</strong>
              </span>
              kaar
            </a>
          </div>
          <div className="right-component">
            <form className="d-flex" role="search">
              <button
                className="btn btn-outline-danger btn-search"
                onClick={handleLogout}
                type="submit"
              >
                Log Out
              </button>
            </form>
          </div>
        </div>
      </nav>


      <div className="patron-top-image">
        <img src="assets/images/patron-dashboard-bg.png"></img>
      </div>
      <div className="patron-profile">
        <div className="patron-profile-image">
          <img src="assets/images/Linkb11.jpeg" />
        </div>
        <h2 className="patron-name">
          {/* {artistname} {artistlname} */}
        </h2>
        <p>{`${userInfo.fname} ${userInfo.mname} ${userInfo.lname}`}</p>
      </div>
      <div className="dashboard-top">
        <div className="patron-profile-details">
          <h1>Your Information</h1>
          <table className="patron-detail-table">
            <tbody>
              {size > 996 ? (
                <tr>
                  <td>Firstname: </td>
                  <td>
                    <input readOnly value={userInfo.fname} />
                  </td>
                  <td style={{ columnGap: "1rem" }}>Lastname: </td>
                  <td>
                    <input readOnly value={userInfo.lname} />
                  </td>
                </tr>
              ) : (
                <>
                  <tr>
                    <td>Firstname: </td>
                    <td>
                      <input readOnly contentEditable="false" value={userInfo.fname} />
                    </td>
                  </tr>
                  <tr>
                    <td>Lastname: </td>
                    <td>
                      <input readOnly value={userInfo.lname} />
                    </td>
                  </tr>
                </>
              )}
              {size > 996 ? (
                <tr>
                  <td>Phone Number: </td>
                  <td>
                    <input readOnly value={userInfo.phone} />
                  </td>
                  <td>Email: </td>
                  <td>
                    <input readOnly value={userInfo.email} />
                  </td>
                </tr>
              ) : (
                <>
                  <tr>
                    <td>Phone Number: </td>
                    <td>
                      <input readOnly value={userInfo.phone} />
                    </td>
                  </tr>
                  <tr>
                    <td>Email: </td>
                    <td>
                      <input readOnly value={userInfo.email} />
                    </td>
                  </tr>
                </>
              )}
              {size > 996 ? (
                <tr>
                  <td>Age: </td>
                  <td>
                    <input readOnly value={userInfo.age} />
                  </td>
                  <td>Gender: </td>
                  <td>
                    <input readOnly value={userInfo.gender} />
                  </td>
                </tr>
              ) : (
                <>
                  <tr>
                    <td>Age: </td>
                    <td>
                      <input readOnly value={userInfo.age} />
                    </td>
                  </tr>
                  <tr>
                    <td>Gender: </td>
                    <td>
                      <input readOnly value={userInfo.gender} />
                    </td>
                  </tr>
                </>
              )}

              {size > 996 ? (
                <tr>
                  <td>Art: </td>
                  <td>
                    <input readOnly value={userInfo.art} />
                  </td>
                  <td>Link: </td>
                  <td>
                    <input readOnly value={userInfo.link} />
                  </td>
                </tr>
              ) : (
                <>
                  <tr>
                    <td>Art: </td>
                    <td>
                      <input readOnly value={userInfo.art} />
                    </td>
                  </tr>
                  <tr>
                    <td>Link : </td>
                    <td>
                      <input readOnly value={userInfo.link} />
                    </td>
                  </tr>
                </>
              )}
              <tr>
                <td>Location: </td>
                <td>
                  <input readOnly value={userInfo.location} />
                </td>
              </tr>
            </tbody>
          </table>
          <div className="table-btns">

            <Link to="/Edit_ArtLover_profile">Edit Profie</Link>
          </div>
        </div>

      </div>




    </div>
  )
}
