import React, { Component } from "react";
import { useState } from "react";

import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

function SS_Navbar() {
  const [sidebar, setSidebar] = useState(false);
  const showSidebar = () => setSidebar(!sidebar);

  const navigate = useNavigate();
  const handleLogout = async () => {
    console.log("Logging out");

    localStorage.clear();

    navigate("/Login");
  };

  return (
    <div className="navofart">
      <div className="navbar">
        {/* <FaIcons.FaBars onClick={showSidebar}/> */}
        <div className="menu-bars">
          <i class="fa-solid fa-bars" onClick={showSidebar}></i>
        </div>
      </div>
      <nav className={sidebar ? "nav-menu-active" : "nav-menu"}>
        <ul
          className="nav-menu-items list-group list-group-flush"
          onClick={showSidebar}
        >
          <li className="navbar-toggle ">
            <div className="menu-bars">
              <i class="fa-solid fa-xmark"></i>
            </div>
          </li>
          <li className="nav-text list-group-item">
            <Link to="/Special_services_dashboard" className="menu-bar-options">
              Dashboard
            </Link>
          </li>
          {/* <li className="nav-text list-group-item">
            <Link to="/Artist_profile" className="menu-bar-options">
              Profile view
            </Link>
          </li> */}
          <li className="nav-text list-group-item">
            <Link to="/SS_Edit_Profile" className="menu-bar-options">
              Edit profile
            </Link>
          </li>
          <li className="nav-text list-group-item">
            <Link to="/SS_upload_opportunities" className="menu-bar-options">
              Upload courses
            </Link>
          </li>
         
        
          <li className="nav-text list-group-item">
            <Link to="/Course" className="menu-bar-options">
              Latest Courses
            </Link>
            {/* <li className="nav-text list-group-item">
                    <Link to="/Community_listing" className="menu-bar-options">Community</Link>
           </li> */}
          </li>
         
          <li className="nav-text list-group-item">
            <Link
              to="/Login"
              onClick={handleLogout}
              type="submit"
              className="menu-bar-options"
            >
              Logout
            </Link>
          </li>

        
        </ul>
      </nav>
    </div>
  );
}

export default SS_Navbar;
