import React from 'react'
import { Link } from 'react-router-dom'
import './Frontpage.css'
import './Dashboard.css'
import Navbar_frontpage from './Navbar_frontpage'
import Footer from './Footer'

export default function Join() {
  return (
    <div>
        <div className='backimg'>


         {/* header */}
         <Navbar_frontpage/>


         <div class="container">
        <div class="col-sm-12">
          <div class="section-header text-center mt-5 ">
          <h3 className="shadow p-3 mb-5   "style={{background:"#e21818"}}>
                <span className="text-light">
                  <strong>Join us</strong>
                </span>
              </h3>
          </div>
        </div>
        <div className="join_us" style={{textAlign:'justify'}}>
        <h5> eKalakaar is a passionate team that is dedicated to promoting art and culture in India. If you share our passion and would like to contribute to our mission, there are several ways you can get involved. <br />
        </h5>
        <h4 className="text-danger h3 ">
                  <strong>Volunteer:</strong>
                </h4>
            <p class="h5"> ✔As a volunteer, you can support eKalakaar by helping us with various tasks such as artist onboarding, content creation, social media promotion, event organization, and more. Volunteers play an important role in helping us achieve our goals, and we welcome anyone who is passionate about promoting art and culture.
            </p>
                <h4 className="text-danger h3 ">
                  <strong>Intern:</strong>
                </h4>
                <p class="h5"> ✔If you're a student or graduate looking to gain experience in the arts and culture industry, eKalakaar offers internship opportunities. As an intern, you'll work with our team on various projects and initiatives, gaining valuable skills and experience that will help you in your future career.
                </p>
                <h4 className="text-danger h3 ">
                  <strong>Work with us: </strong>
                </h4>
                <p class="h5">  ✔If you're interested in a more long-term commitment, we offer opportunities to work with us. We're always looking for talented individuals who share our passion for promoting art and culture in India. Whether you're interested in artist management, content creation, social media marketing, event planning, or any other aspect of our work, we would love to hear from you.
                </p> </div>
        
      </div>

  
  <section id="Contribute" class="section-padding associate">
    <div class="container associate">
        <div class="col-md-12">
            <div class="section-header text-center pb-5">
            <h3 className="shadow p-3 mb-5   "style={{background:"#e21818"}}>
                <span className="text-light">
                  <strong>Associate with us</strong>
                </span>
              </h3>
                <p class="h5"> No matter how you choose to get involved, eKalakaar offers a supportive and collaborative environment where you can learn and grow while making a meaningful contribution to the arts and culture community in India.
                </p>
            </div>

        </div>
        <div class="row">

        </div>

        <div class="row">

            <div class="col-md-12" style={{"padding-left": "10%", "padding-right":"10%"}}>
                <form class="row g-3" action="partials/_handleContact.php" method="post">
                    <div class="col-md-6">
                        <label for="validationDefault01" class="form-label">Name</label>
                        <input type="text" class="form-control" id="validationDefault01" name="Name" value=""
                            required/>
                    </div>
                    <div class="col-md-6">
                        <label for="validationDefault01" class="form-label">Contact Number</label>
                        <input type="text" class="form-control" id="validationDefault01" name="contactNumber"
                            value="" required/>
                    </div>

                  

                    <div class="col-md-6">
                        <label for="validationDefaultUsername" class="form-label">Email Id</label>
                        <div class="input-group">
                            <span class="input-group-text" id="inputGroupPrepend2">@</span>
                            <input type="text" class="form-control" name="email" id="validationDefaultUsername"
                                aria-describedby="inputGroupPrepend2" required/>
                        </div>
                    </div>

                    
                    
                    <div class="col-md-6">
                        <label for="validationDefault03" class="form-label">Location</label>
                        <input type="text" class="form-control" name="location" id="validationDefault03" required/>
                    </div>


              
                    
                    <div class="row" style={{"margin-top":"20px "}}>
                        <div class="col-md-6">
                            <label for="exampleFormControlTextarea1" class="form-label">How you want to Associate with us?
                            
                            </label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" name="award"
                                rows="3"></textarea>

                        </div>
                    </div>




                    <div class="col-12"   style={{"margin-top":"30px " }}>
                        <button class="btn btn-danger "  style={{"text-align": "center"}}type="submit">Submit form</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</section>




{/* <!-- conatct --> */}
  <Footer/>

      </div>

    </div>
  )
}
