import { useState, useEffect } from 'react';
import Navbar from './Navbar';
import ArtistNavbar from './Artist_Navbar';
import './Navbar.css';
import { useNavigate, Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCircleDot,
  faFloppyDisk,
  faFile,
} from '@fortawesome/free-solid-svg-icons';

export default function Artist_search_opportunity() {
  const [opportunities, setOpportunities] = useState([]);

  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');

    fetch('http://localhost:4000/api/opportunity/all', {
      method: 'GET',
      headers: { Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setOpportunities([...res.data]);
      });
  }, [opportunities]);

  const handleEditOpportunity = (opportunityId) => {
    navigate(`/Edit_Opportunity/${opportunityId}`);
  };

  const handleViewOpportunity = (opportunityId) => {
    navigate(`/Artist_viewdetailopp/${opportunityId}`);
  };

  // console.log(opportunities);

  return (
    <div>
      <nav
        className="navbar bg-light sticky-top"
        style={{ backgroundColor: 'white' }}
      >
        <div className="container-fluid">
          <div className="left-components">
            <ArtistNavbar className="nav_artist" />
            <a className="navbar-brand" href="/">
              <span className="text-danger text">
                {' '}
                <strong>ekala</strong>
              </span>
              kaar
            </a>
          </div>
          <div className="right-component">
            <form className="d-flex" role="search">
              <input
                className="form-control me-2 "
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search "
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>

      <div className="container mt-5 mb-5 d-flex justify-content-center flex-column gap-5">
        {<h1 class="card-title p-3">Total: {opportunities.length} Oppportunities Seeking Talent</h1>}
        {opportunities.map((opp, index) => {
          return (
            <div class="card" key={index}>
              <div class="card-header h2 bg-danger">{opp.position}</div>
              <div class="card-body ">
                {/* <h5 class="card-title">{opp.about}</h5> */}
                {/* <p class="card-text">{opp.requirements}</p> */}
                <p class="card-text text-dark ">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Location:</strong> {opp.location}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Mode:</strong> {opp.mode}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Openings:</strong> {opp.number}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Language:</strong> {opp.language}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon icon={faCircleDot} className="text-danger" />
                  <strong> Budget:</strong> {opp.budget}
                </p>
                <div className="d-flex float-right">
                  <button
                    className="btn btn-outline-danger btn-sm"
                    onClick={() => handleEditOpportunity(opp._id)}
                  >
                    <FontAwesomeIcon icon={faFloppyDisk} className="mr-1" />{' '}
                    Save
                  </button>
                  <button className="btn btn-outline-danger btn-sm ml-2"
                  onClick={() => handleViewOpportunity(opp._id)}>

                    <FontAwesomeIcon icon={faFile} className="mr-1" />{' '}
                    Apply/View   
                  </button>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
