import React from 'react';
import Navbar from './Navbar';
import './Navbar.css';
import { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faMapMarkerAlt,
  faUserTie,
  faBriefcase,
} from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';
import './Patron.css';

export default function Applied_artist_application() {
  const [applications, setApplications] = useState([]);

  useEffect(() => {
    const token = localStorage.getItem('token');

    fetch('http://localhost:4000/api/opportunity/all/applications', {
      method: 'GET',
      headers: { Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setApplications([...res.data]);
      });
  }, [applications]);

  // console.log(applications);

  return (
    <div>
      <nav className="navbar bg-light">
        <div className="container-fluid">
          <div className="left-components">
            <Navbar />
            <Link
              className="navbar-brand"
              style={{ marginLeft: '57px', position: 'fixed' }}
              to="index.html"
            >
              <span className="text-danger text">
                <strong>ekala</strong>
              </span>
              kaar
            </Link>
          </div>
          <div
            className="right-component"
            style={{ right: '10px', top: '10px', position: 'fixed' }}
          >
            <form className="d-flex" role="search">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search artist"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-danger btn-search"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>

      <div className="container mt-3 mb-5 d-flex justify-content-center flex-column gap-5">
       
          {applications.length === 0 && (
            <h5 className="card-title pb-2">There is no applications found!!!</h5>
          )}

             {<h5 class="card-title "> <h1> Talented Artists  </h1></h5>}
          {applications.length > 0 &&
          
            applications.map((application, index) => {
             
              return (   
                <>
               
                {/* <div className="col-sm-6" key={index}>                  
                  <div className="card mb-4">
                 
                    <div className="card-body">
                      <h5 className="card-title pb-2">
                        {application?.artist?.name}{' '}
                        {application?.artist?.middleName}{' '}
                        {application?.artist?.lastName}
                      </h5>
          
                      <p className="card-position mb-2">
                        <FontAwesomeIcon
                          icon={faUserTie}
                          className="mr-2 text-danger"
                        />
                        {application?.opportunity?.position}
                      </p>
                      <p className="card-location mb-2">
                        <FontAwesomeIcon
                          icon={faMapMarkerAlt}
                          className="mr-2 text-danger"
                        />
                        {application?.artist?.artist?.location}
                       
                      </p>
                      <p className="card-experince  mb-2">
                        <FontAwesomeIcon
                          icon={faBriefcase}
                          className="mr-2 text-danger"
                        />
                        {application?.artist?.artist?.experince}
                      </p>
                      <div className="d-flex justify-content-between pt-2 align-items-center">
                        <a href="" className="btn btn-danger">
                          View Application
                        </a>
                      </div>
                    </div>
                  </div>
                </div> */}



              {/* Artist applied application */}
              <div class="card " key={index}>
              <div class="card-header h2 bg-danger text-capitalize"> Artist -  {application?.artist?.name}{' '}
                        {application?.artist?.middleName}{' '}
                        {application?.artist?.lastName}</div>
              <div class="card-body">
               
                <p class="card-text  text-dark">
                  <FontAwesomeIcon   icon={faUserTie} className="text-danger" />
                  <strong> Position:</strong> {application?.opportunity?.position}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon   icon={faMapMarkerAlt} className="text-danger" />
                  <strong> Location:</strong> {application?.artist?.artist?.location}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon  icon={faBriefcase} className="text-danger" />
                  <strong> Experience:</strong> {application?.artist?.artist?.experince}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon  icon={faBriefcase} className="text-danger" />
                  <strong> Category:</strong> {application?.artist?.artist?.category}
                </p>
                <p class="card-text text-dark">
                  <FontAwesomeIcon  icon={faBriefcase} className="text-danger" />
                  <strong> Age:</strong> {application?.artist?.artist?.age} yrs
                </p>
                <div className="d-flex ">
                        <a href="" className="btn btn-lg btn-danger">
                          View Application
                        </a>
                      </div>
              </div>
            </div>

                </>
              );
            })}
        </div>
      </div>
    

  
  );
}
